////////////////////////////
// !CORE / MIXINS / OVERFLOW
////////////////////////////


// !Visible
@mixin overflow-visible($imp:null) { overflow: visible $imp; }
@mixin overflow-x-visible($imp:null) { overflow-x: visible $imp; }  // -x
@mixin overflow-y-visible($imp:null) { overflow-y: visible $imp; }  // -y


// !Hidden
@mixin overflow-hidden($imp:null) { overflow: hidden $imp; }
@mixin overflow-x-hidden($imp:null) { overflow-x: hidden $imp; }  // -x
@mixin overflow-y-hidden($imp:null) { overflow-y: hidden $imp; }  // -y


// !Auto
@mixin overflow-auto($imp:null) {
  overflow: auto $imp;
  -webkit-overflow-scrolling: touch;
}

@mixin overflow-x-auto($imp:null) {
  overflow-x: auto $imp;
  -webkit-overflow-scrolling: touch;
}

@mixin overflow-y-auto($imp:null) {
  overflow-y: auto $imp;
  -webkit-overflow-scrolling: touch;
}


// !Scroll
@mixin overflow-scroll($imp:null) {
  overflow: scroll $imp;
  -webkit-overflow-scrolling: touch;
}


// !Overflow-X: Scroll
@mixin overflow-x-scroll($imp:null) {
  overflow-x: scroll $imp;
  -webkit-overflow-scrolling: touch;
}


// !Overflow-Y: Scroll
@mixin overflow-y-scroll($imp:null) {
  overflow-y: scroll $imp;
  -webkit-overflow-scrolling: touch;
}
