// !Common Footer Styles

// !Local Footer
// .shdpx .dcf-footer-local {
// }


// !Global Footer
// .shdpx .dcf-footer-global {
// }

.shdpx {
  .dcf-footer {
    border-top: 2px solid #ccc;
  }

  .dcf-footer-global {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    @include pt-6;
    @include pb-5;

    .students {
      flex: 0.2 1 25%;
      padding-right: 5%;
    }

    .resources {
      flex: 0.2 1 23.4%;
      padding-right: 5%;
    }

    .affiliates {
      flex: 0.2 1 33%;
      padding-right: 2%;
    }

    .contact {
      @include mt-4;
      flex: 0.18 1 100%;
    }
  }

  .footer-header {
    color:  $color-brand-alpha;
    font-weight: normal;
    font-size: #{ms(2)}em;
    @include font-bold-cn;
    @include uppercase;
    @include mb-1;
  }

  .footer-text {
    color: $black;
    @include font-cn;
  }

  .footer-link-list li {
    @include mb-3;
    @include lh-2;
  }

  .footer-link-list a {
    color: $black;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  .footer-link-contact li {
    @include mb-2;
    @include lh-2;
  }

  .footer-link-list.footer-link-contact a, .cms-login, .footer-phone{
    border-bottom: 1px dotted $black;
    text-decoration: none;
    color: $black;

    &:hover {
      border-bottom: none;
      text-decoration: none;
    }
  }

  .contact {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-content: flex-start;

    .footer-header {
      flex: 1 0 100%;
    }

    .social {
      flex: 0 1 100%;
      @include d-flex;
      flex-flow: column wrap;
      align-items: flex-start;
      justify-content: flex-end;
    }

    .svg-container {
      height: 3em;
      max-width: 10em;
      @include mt-3;
      fill: #555656;
			position: relative;
			display: flex;

			.univ-logo {
				width: 100%;
				height: 100%;
			}
    }

    .social-icons {
      @include mt-5;
      width: 100%;
      display: flex;
      justify-content: flex-start;
    }

    .social-icons a {
      @include d-inline-block;
      width: 8%;
      max-width: 1.8em;
    }

    .social-icons img {
      @include d-block;
    }

    .social-icons a:not(:first-child) {
      margin-left: 7.75%;
    }
  }
}

@include mq(576px, min, width) {
  .shdpx {
    .contact {
      .svg-container {
        align-self: flex-end;
        width: 50%;
      }

      .social {
        flex: 1;
      }

      .social-icons {
        justify-content: flex-end;
        @include mt-2;
      }
    }
  }

}

//@include mq(731px, min, width) {
//  .shdpx {
//
//  }
//}

@include mq(1000, min, width) {
  .shdpx {
    .dcf-footer-global {
      .students {
        flex: 0 1 17%;
      }

      .resources {
        flex: 0 1 17%;
      }

      .affiliates {
        flex: 0.2 1 17%;
      }

      .contact {
        flex: 0.5 1 37%;
        @include mt-0;
        margin-left: auto;
      }

      .contact .contactInfo {
        flex: 3;
      }

      .contact .contactInfo .address {
        @include lh-2;
        @include d-inline-block;
      }

      .contact .social {
        align-items: flex-start;
        flex: 2;
      }
    }

    .footer-link-list li {
      @include mb-2;
      line-height: auto;
    }

    .contact .social-icons a {
      width: 14%;;
      max-width: 1.35em;
    }

    .contact .svg-container {
      width: 70%;
    }
  }
}

@supports (display:grid) {
	.svg-container {
		height: auto !important;
	}
}
